.advantages {
  background-image: url("../img/background/advantages-background.png");
  background-color: $color-solitude;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;

  transition: background-color $trans-600;

  @include retina {
    background-image: url("../img/background/advantages-background@2x.png");
  }

  @include vp-768 {
    min-height: 588px;
    background-image: url("../img/background/advantages-background-tablet.png");

    @include retina {
      background-image: url("../img/background/advantages-background-tablet@2x.png");
    }
  }

  @include vp-320 {
    background-image: url("../img/background/advantages-background-mobile.png");

    @include retina {
      background-image: url("../img/background/advantages-background-mobile@2x.png");
    }
  }
}

.advantages__container {
  padding: 112px 20px 120px;

  @include tablet-version-range {
    padding: 90px 45px 100px;
  }
}

.advantages h2 {
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  text-align: center;
  margin: 0 0 51px 0;

  @include tablet-version-range {
    font-size: 26px;
    line-height: 39px;
    margin: 0;
    margin-bottom: 49px;
  }

  @include mobile-version-range {
    font-size: 24px;
    line-height: 36px;
    margin-top: -8px;
    margin-bottom: 31px;
  }
}

/* ============ advantages__list */

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(206px, 1fr));
  gap: 40px;

  @include tablet-version-range {
    gap: 30px;
  }

  @include mobile-version-range {
    display: block;
  }
}

/* ============ advantages__item */

.advantages__item {
  display: flex;
  flex-direction: column;
  background-color: $color-alice-blue;
  padding: 30px 30px 50px 30px;
  min-height: 241px;

  transition: background-color $trans-600, color $trans-default;

  @include mobile-version-range {
    margin-bottom: 31px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.advantages__item h3 {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  margin: 0;
  margin-bottom: 20px;

  @include tablet-version-range {
    line-height: 25px;
  }

  @include mobile-version-range {
    max-width: 190px;
  }
}

.advantages__item p {
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

.advantages__item a {
  order: -1;
  margin-bottom: 25px;
}

.advantages__item svg {
  width: 100%;
  height: 100%;
  fill: $color-tangaroa;
  transition: fill $trans-600;
}

/* Отдельные стили для каждого пункта(блока преимуществ), чтобы попасть в Pixel perfect */

//advantages__item--cruise-ship

.advantages__item--cruise-ship {
  padding: 28px 30px 28px;

  @include tablet-version-range {
    padding: 30px 15px 20px 20px;
  }

  @include mobile-version-range {
    padding: 24px 15px 19px 20px;
  }
}

.advantages__item--cruise-ship h3 {
  margin: 0 0 8px;

  @include mobile-version-range {
    margin: 0 0 6px;
  }
}

.advantages__item--cruise-ship a {
  width: 27px;
  height: 32px;
  margin-bottom: 19px;

  @include tablet-version-range {
    width: 21px;
    height: 26px;
    margin-bottom: 20px;
    margin-top: -4px;
  }

  @include mobile-version-range {
    width: 21px;
    height: 26px;
    margin-bottom: 18px;
  }
}

//advantages__item--all-inclusive

.advantages__item--all-inclusive {
  padding: 32px 30px 21px;

  @include tablet-version-range {
    padding: 30px 15px 20px 20px;
  }

  @include mobile-version-range {
    padding: 30px 15px 18px 20px;
  }
}

.advantages__item--all-inclusive h3 {
  margin: 0 0 8px;
}

.advantages__item--all-inclusive a {
  width: 38px;
  height: 21px;

  @include tablet-version-range {
    width: 33px;
    height: 19px;
    margin-bottom: 22px;
  }

  @include mobile-version-range {
    width: 33px;
    height: 19px;
    margin-bottom: 20px;
  }
}

//advantages__item--zodiac-excursions

.advantages__item--zodiac-excursions {
  padding: 36px 30px 26px;

  @include tablet-version-range {
    padding: 30px 17px 20px 20px;
  }

  @include mobile-version-range {
    padding: 30px 17px 18px 20px;
  }
}

.advantages__item--zodiac-excursions h3 {
  margin: 0 0 1px;

  @include tablet-version-range {
    margin: 0 0 8px;
  }

  @include mobile-version-range {
    margin: 0 0 8px;
  }
}

.advantages__item--zodiac-excursions a {
  width: 42px;
  height: 15px;
  margin-bottom: 35px;

  @include tablet-version-range {
    width: 38px;
    height: 13px;
    margin-bottom: 28px;
  }

  @include mobile-version-range {
    width: 38px;
    height: 13px;
    margin-bottom: 26px;
  }
}

/* Dark mode */
.advantages.dark-mode {
  background-color: $color-midnight;
}

.advantages.dark-mode h2 {
  color: $body-azure;
}

.advantages.dark-mode .advantages__item {
  background-color: $body-black-pearl;
  color: $body-azure;
}

/* Добавляем тень к каждому элементу преимуществ */
.advantages.dark-mode .advantages__item--cruise-ship {
  box-shadow:
    rgba(46, 186, 240, 0.4) -5px 5px,
    rgba(46, 231, 240, 0.3) -10px 10px,
    rgba(46, 240, 222, 0.2) -15px 15px,
    rgba(46, 240, 150, 0.1) -20px 20px,
    rgba(46, 240, 186, 0.05) -25px 25px;

    @include vp-768 {
      box-shadow: none;
    }
}

.advantages.dark-mode .advantages__item--all-inclusive {
  box-shadow:
    rgba(240, 46, 170, 0.4) 0 5px,
    rgba(240, 46, 170, 0.3) 0 10px,
    rgba(240, 46, 170, 0.2) 0 15px,
    rgba(240, 46, 170, 0.1) 0 20px,
    rgba(240, 46, 170, 0.05) 0 25px;

    @include vp-768 {
      box-shadow: none;
    }
}

.advantages.dark-mode .advantages__item--zodiac-excursions {
  box-shadow:
    rgba(46, 97, 240, 0.4) 5px 5px,
    rgba(46, 222, 240, 0.3) 10px 10px,
    rgba(46, 177, 240, 0.2) 15px 15px,
    rgba(46, 240, 222, 0.1) 20px 20px,
    rgba(46, 97, 240, 0.05) 25px 25px;

    @include vp-768 {
      box-shadow: none;
    }
}


.advantages.dark-mode svg {
  fill: $body-azure;
}
